.wrapper {
  position: relative;
  max-width: 324px;
  width: 100%;
  overflow: hidden;
  text-align: right;
  border-radius: 10px;
  margin: 15px 10px 20px;
  font-family: 'OpenSansHe', Helvetica, Arial, sans-serif !important;
  box-shadow: 0 2px 16px 0 rgba(0, 0, 0, 0.04);
  align-self: start;
}

.wrapper::after {
  position: absolute;
  top: 88px;
  right: 16px;
  background-color: #f19a34;
  padding: 4px 8px;
  content: 'Hotel + flight';
  font-weight: 600;
  font-size: 13px;
  color: #fff;
  border-radius: 4px;
}

.image {
  width: 100%;
  height: 165px;
}

.blur {
  position: relative;
  width: 100%;
  height: 46px;
  filter: blur(3px);
  overflow: hidden;
  display: flex;
  align-items: flex-end;
  margin-bottom: -3px;
  margin-top: -5px;
}

.blurWrapper {
  top: -40px;
  position: relative;
  overflow: hidden;
  display: flex;
  align-items: flex-end;
}

.blur::before {
  position: absolute;
  left: 0;
  top: 0;
  bottom: 0;
  right: 0;
  background-color: rgba(0, 0, 0, 0.45);
  content: '';
}

.blurImage {
  height: 165px;
  width: 100%;
  display: block;
}

.location {
  font-size: 13px;
  padding-left: 15px;
  display: block;
}

.caption {
  margin-top: -81px;
  padding: 11px;
  padding-right: 16px;
  position: relative;
  color: #fff;
  display: flex;
  justify-content: flex-end;
}

.content {
  display: flex;
  border-bottom: 1px solid #eeeff2;
  justify-content: space-between;
  padding: 12px 16px;
  flex-wrap: wrap;
}
.contentWrap {
  display: flex;
  justify-content: space-between;
  width: 100%;
}

.left {
  display: flex;
  flex-direction: column;
  text-align: left;
  align-items: flex-start;
}

.left > span:nth-child(1) {
  color: #8f92a0;
  font-size: 12px;
}
.left > span:nth-child(2) {
  color: #4b5066;
  font-size: 16px;
  font-weight: 900;
}
.left > span:nth-child(3) {
  color: #8f92a0;
  font-size: 12px;
  font-style: italic;
}

.right {
  display: flex;
  flex-direction: column;
  text-align: right;
  align-items: flex-end;
}

.right > span:nth-child(1) {
  color: #4b5066;
  font-size: 14px;
  font-weight: 500;
  text-align: right;
  margin-bottom: 5px;
}
.right > span:nth-child(2) {
  color: #8f92a0;
  font-size: 12px;
  font-weight: 500;
  text-align: right;
  margin-top: 8px;
}
.right > span:nth-child(3) {
  color: #4b5066;
  font-size: 14px;
  font-weight: bold;
  line-height: 20px;
  text-align: right;
  margin-top: 16px;
}

.rating {
  margin: 10px 0 8px -3px;
  display: flex;
  /* justify-content: space-around; */
}

.detailsButton {
  color: #9ea2b3;
  font-size: 12px;
  font-weight: bold;
  text-align: right;
  display: flex;
  align-items: center;
  cursor: pointer;
}

.moreOffers {
  text-decoration: none;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #ff9c35;
  font-size: 14px;
  font-weight: bold;
  text-align: right;
  padding: 16px;
}

.details {
  padding: 14px 12px;
  background-color: #fff;
  border-radius: 6px;
  margin: 12px 0;
  width: 100%;
}

.detailsDate {
  display: flex;
  justify-content: space-between;
  margin-bottom: 10px;
}

.detailsDate:nth-child(3) {
  margin-top: 10px;
}

.detailsDate > span:nth-child(1) {
  color: #4b5066;
  font-size: 12px;
  font-weight: bold;
  line-height: 15px;
}

.detailsDate > span:nth-child(2) {
  color: #6e8295;
  font-size: 15px;
  font-weight: bold;
  line-height: 19px;
}

.detailsTime {
  display: flex;
  justify-content: space-between;
  border-bottom: 1px solid #ccc;
}

.detailsTime:nth-child(4) {
  border: none;
}

.detailsLanding {
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin-bottom: 10px;
  text-align: center;
}
.detailsLanding span:nth-child(1) {
  color: #4b5066;
  font-size: 11px;
  line-height: 18px;
}
.detailsLanding span:nth-child(2) {
  color: #8f92a0;
  font-size: 13px;
  line-height: 16px;
}

.detailsHour {
  margin: 0 2px;
  color: #faa704;
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.detailsHour span:nth-child(1) {
  color: #4b5066;
  font-size: 11px;
  font-weight: 600;
  line-height: 15px;
  margin-bottom: -3px;
}

.detailsHour span:nth-child(3) {
  margin-top: -5px;
  color: #9ea2b3;
  font-family: 'Open Sans';
  font-size: 10px;
  line-height: 14px;
  text-align: center;
}

.line {
  height: 2px;
  width: 63px;
  border-radius: 2px;
  background-color: #faa704;
  content: '';
  margin: 0 3px;
}

.accomodation {
  width: 100%;
  padding: 14px 12px;
  background-color: #fff;
  border-radius: 6px;
  margin: 12px 0;
}

.accomodation p {
  color: #6e8295;
  font-size: 15px;
  font-weight: bold;
  line-height: 19px;
}

.accomodationContent {
  display: flex;
  justify-content: flex-end;
  align-items: flex-start;
}

.accomodationImage {
  height: 80px;
  width: 80px;
  border-radius: 6px;
  margin-left: 12px;
}

.accomodationRating {
  display: flex;
  width: calc(100% - 80px);
  flex-direction: column;
  align-self: flex-end;
  align-self: start;
  margin-top: -2px;
}

.accomodationRating .rating {
  justify-content: flex-end;
  margin-top: 0;
}

.hotelName {
  color: #242529;
  font-size: 16px;
  font-weight: bold;
  line-height: 20px;
}

.reviewScore {
  margin-top: 4px;
}

.reviewScore span:nth-child(1) {
  color: #242529;
  font-weight: bold;
  font-size: 12px;
  line-height: 17px;
}

.reviewScore span:nth-child(2) {
  color: #8f92a0;
  font-size: 12px;
  line-height: 17px;
  font-weight: 300;
}

.reviewScore span:nth-child(3) {
  color: #FFFFFF;
  font-family: "Open Sans";
  font-size: 12px;
  font-weight: bold;
  line-height: 17px;
  text-align: right;
  border-radius: 6px;
  background-color: #36C02B;
  display: inline-block;
  padding: 3px 6px;
}

.makeReservation {
  color: #ffffff;
  font-size: 14px;
  font-weight: 600;
  line-height: 19px;
  text-align: center;
  padding: 13px;
  width: 100%;
  background-color: #ff9c35;
  border: none;
  border-radius: 5px;
}
