.formWrap {
  padding: 24px 20px;
  background-color: white;
  margin-bottom: 64px;
}

.form {
  display: flex;
  justify-content: space-between;
  align-items: center;
  max-width: 1360px;
  margin: auto;
}

.button {
  color: #ffffff;
  font-size: 14px;
  font-weight: 600;
  line-height: 19px;
  text-align: center;
  padding: 18px;
  width: 100%;
  background-color: #ff9c35;
  border: none;
  border-radius: 5px;
  width: 160px;
}

@media screen and (max-width: 1400px) {
  .form {
      justify-content: space-around;
      flex-wrap: wrap;

  }
  .button {
    order: 1;
  }
}

@media screen and (max-width: 410px) {
  .formWrap {
      display: none;
  }
}