.formWrap {
  background-color: white;
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  overflow-y: scroll;
  width: 100%;
  height: 100%;
  z-index: 3;
}

.modalHeader {
  display: flex;
  align-items: center;
  padding: 6px 12px 5px;
  justify-content: space-between;
  border-bottom: 1px solid #E9E9E9;
}

.form {
  display: flex;
  justify-content: space-between;
  align-items: center;
  max-width: 1360px;
  margin: auto;
}

.button {
  color: #ffffff;
  font-size: 14px;
  font-weight: 600;
  line-height: 19px;
  text-align: center;
  padding: 16px;
  width: 100%;
  background-color: #ff9c35;
  border: none;
  border-radius: 5px;
  max-width: 85%;
  position: fixed;
  bottom: 20px;
  left: 50%;
  transform: translateX(-50%);
}



.tabs {
  display: flex;
  align-items: flex-start;
  width: 100%;
}

.tabList {
  list-style: none;
  padding: 0;
  border-right: 1px solid #E9E9E9;
  min-width: 80px;
}

.tab {
  max-height: 80px;
  height: 80px;
  max-width: 80px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  padding:0 ;
  color: #9EA2B3;
  border-bottom: 1px solid #E9E9E9;

  span {
    margin-top: 5px;
    font-size: 13px;
    line-height: 18px;
    text-align: center;
  }
}

.tabPanel {
  padding: 16px;
  flex: 1;
  width: 100%;
}



.tabActive {
  background-color: #FAA306;
  color: white;
}

.tabPanelText {
  display: flex;
  justify-content: space-between;
  .label {
    color: #9EA2B3;
    font-size: 12px;
    line-height: 15px;
  }
}

.clearFilter {
  color: #6E8295;
  font-size: 13px;
  font-weight: 600;
  line-height: 18px;
  padding-right: 4px;
}