.title {
  text-align: right;
  max-width: 1360px;
  margin: 30px auto 00px;
  padding: 0 20px;
  font-size: 24px;
}

.filterButton {
  display: none;
  height: 48px;
  width: 48px;
  background-color: rgba(250,167,4,1);
  box-shadow: 0 2px 8px 0 rgba(0,0,0,0.15);
  border-radius: 50%;
  border: none;
  position: fixed;
  bottom: 24px;
  right: 24px;
  z-index: 2;
  outline: none;
  span {
    width: 22px;
    height: 2px;
    background-color: #fff;
    position: absolute;
    top: 16px;
    left: 50%;
    transform: translateX(-50%);
    &:nth-child(2) {
      top: 23px;
      width: 14px;
    }
    &:nth-child(3) {
      top: 30px;
      width: 7px;
    }
  }
}

@media screen and (max-width: 410px) {
  .filterButton {display: block;}
  .title {
    text-align: center;
    font-size: 24px;
  }
}

