.selectWrap {
  position: relative;
  i {
    position: absolute;
    color: #777;
    top: 20px;
    left: 16px;
  }
}

.select {
  width: 100%;
  background: transparent;
  border: none;
  font-size: 16px;
  outline: none;
  padding: 24px 16px 8px 38px;
}

.option {
  outline: none;
  border-bottom: 1px solid grey;
  padding: 5px;
}



.label {
  position: absolute;
  text-align: right;
  right: 40px;
  top: 12px;
  font-size: 12px;
  color: #999;
}
